<template>
  <li class="card-list-item" @click="goToRecommend(item.id)">
    <div class="pic-box">
      <img :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=246x0`" />
      <span class="play-count">
        <img
          style="width: 12px"
          src="data:image/svg+xml;base64,"
          alt=""
        />
        {{ item.playCount | calcCount }}
      </span>
    </div>
    <slot class="te"></slot>
    <!-- {{item.id}} -->
  </li>
</template>

<script>
export default {
  name: "CartItem",
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  methods: {
    goToRecommend(id) {
      this.$router.push("/recommend?id=" + id);
    },
  },
 
  filters: {
    calcCount(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else if (value > 10000) {
        return (value / 10000).toFixed(1) + "万";
      } else {
        return value;
      }
    },
  },
};
</script>
<style lang="less" scoped>
.card-list-item {
  // 占30%
  flex-basis: 30%;
  // 一组
  flex-grow: 1;
  margin-bottom: 16px;
  &:nth-child(3n + 2) {
    margin: 0 3px;
  }
  .pic-box {
    position: relative;
    height: 123px;
    box-shadow: 0 1px 3px 0 rgba(0,0,0) inset;
    span.play-count {
      position: absolute;
      top: 3px;
      right: 5px;
      color: #fff;
      font-size: 12px;
    }
  }
  .desc {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding: 6px 2px 0 6px;
    min-height: 30px;
    line-height: 1.2;
    font-size: 13px;
    padding: 6px 2px 0;
  }
}
</style>